.main {
    width: 50%;
    margin: 50px auto;
}

.has-search .form-control {
    padding-left: 2.375rem;
}

.has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
}

.container h1 span #typed-element{
    color: white;
    font-size: large;
}


@media screen and (max-width: 600px) {
    .subheader, .logos {
      display: none;
    }
  }
  